

::-webkit-scrollbar  { 
  display: none;
}

#root {
  font-family: 'Source Sans Pro', sans-serif;
  position: absolute;
  left: 0;
  top: 0;
  height: 1000px;
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
  -webkit-transform-origin: top left;
      -ms-transform-origin: top left;
          transform-origin: top left;
  background-color: grey;
  color: #0A3350;
  overflow: hidden;
}


